				this.col = 'blue'
				this.$emit('colorData',this.col)
			}
		}
	}
</script>

<style lang="scss">
	.zzj{
		width: 500px;
		height: 500px;
		display: inline-block;
		border: 1px solid red;
		.zizujian1{
			border-radius: 10px;
			border: 0;
			width: 200px;
			height: 50px;
		}
